var app = require("../../common/common.js");

var per_page = 0;
var isfirst = true;
var catid = 0;
 
export default {
 
	data:function(){
		return {
			pageLoad:false, 
			pageHide:false,
			pageData:{},
		}
		
	},
	onLoad: function (option) {
	 
		uni.setNavigationBarTitle({
			title: '我的消息'
		});
		this.getPage();
	},
	onReachBottom: function () {
		this.getList();
	},
	onPullDownRefresh: function () {
		this.refresh();
	},
	methods: {
		getPage: function () {
			var that = this;
			uni.request({
				url: app.apiHost + "?fromapp=wxapp&m=notice&a=my&ajax=1",
				data: {
					authcode: app.getAuthCode()
				},
				success: function (data) {
					if (data.error == 1000) {
						uni.navigateTo({
							url: "/pages/login/index",
						})
					} else {
						isfirst = false;
						that.pageLoad=true;
						that.pageData = data.data.data;
						per_page = data.data.data.per_page;
					}

				}
			})
		},

		getList: function () {
			var that = this;
			if (!isfirst && per_page == 0) return false;
			uni.request({
				url: app.apiHost + "?m=notice&a=my&ajax=1",
				data: {
					per_page: per_page,
					catid: catid,
					authcode: app.getAuthCode()
				},
				success: function (data) {

					if (!data.data.error) {
						if (isfirst) {
							that.pageData.list = data.data.data.list;
							isfirst = false;
						} else {

							that.pageData.list = app.json_add(that.pageData.list, data.data.data.list);
						}
						per_page = data.data.data.per_page;

					}


				}
			})
		},

		refresh: function () {
			this.getPage();
			setTimeout(function () {
				uni.stopPullDownRefresh();
			}, 1000)
		},
		loadMore: function () {
			this.getList();
		}
	},
}
